/* Career Page Styles */

/* Hero Section */
.career-hero {
  background: linear-gradient(135deg, rgba(112, 166, 178, 0.9), rgba(112, 166, 178, 0.7)), 
              url('data:image/svg+xml,<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 1200 600"><rect fill="%23f0f0f0" width="1200" height="600"/><circle fill="%23e0e0e0" cx="200" cy="150" r="80"/><circle fill="%23d0d0d0" cx="800" cy="300" r="60"/><circle fill="%23e5e5e5" cx="1000" cy="100" r="40"/></svg>');
  background-size: cover;
  background-position: center;
  min-height: 70vh;
  display: flex;
  align-items: center;
  position: relative;
  overflow: hidden;
}

.career-hero::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(135deg, rgba(112, 166, 178, 0.8), rgba(112, 166, 178, 0.6));
  z-index: 1;
}

.hero-content {
  position: relative;
  z-index: 2;
  text-align: center;
  color: white;
  padding: 4rem 0;
}

.hero-title {
  font-size: 3.5rem !important;
  font-weight: 700 !important;
  margin-bottom: 1rem !important;
  line-height: 1.2 !important;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.3);
}

.hero-subtitle {
  font-size: 1.5rem !important;
  font-weight: 400 !important;
  margin-bottom: 3rem !important;
  opacity: 0.9;
}

.job-search-form {
  background: white;
  padding: 2rem;
  border-radius: 16px;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.2);
  max-width: 800px;
  margin: 0 auto;
}

.search-button {
  background: linear-gradient(45deg, #FF6B35, #F7931E) !important;
  color: white !important;
  font-weight: 600 !important;
  padding: 1rem 2rem !important;
  border-radius: 8px !important;
  text-transform: none !important;
  font-size: 1.1rem !important;
  box-shadow: 0 4px 15px rgba(255, 107, 53, 0.3) !important;
  transition: all 0.3s ease !important;
}

.search-button:hover {
  transform: translateY(-2px) !important;
  box-shadow: 0 6px 20px rgba(255, 107, 53, 0.4) !important;
}

/* What we do Section */
.what-we-do-section {
  background: linear-gradient(135deg, #E8F4F8, #F0F8FA);
  padding: 6rem 0;
}

.section-title {
  font-size: 2.5rem !important;
  font-weight: 700 !important;
  color: #2C3E50 !important;
  text-align: center;
  margin-bottom: 1rem !important;
}

.section-description {
  font-size: 1.1rem !important;
  color: #5A6C7D !important;
  line-height: 1.6 !important;
  margin-bottom: 2rem !important;
}

.service-card {
  background: white !important;
  border-radius: 16px !important;
  box-shadow: 0 8px 25px rgba(0, 0, 0, 0.1) !important;
  transition: all 0.3s ease !important;
  height: 100% !important;
  border: none !important;
}

.service-card:hover {
  transform: translateY(-8px) !important;
  box-shadow: 0 15px 35px rgba(0, 0, 0, 0.15) !important;
}

.card-title {
  font-weight: 600 !important;
  color: #2C3E50 !important;
  margin-bottom: 1rem !important;
}

.card-description {
  color: #5A6C7D !important;
  line-height: 1.6 !important;
}

/* Why choose section */
.why-choose-section {
  background: white;
  padding: 6rem 0;
}

.feature-item {
  display: flex;
  align-items: flex-start;
  margin-bottom: 2rem;
  gap: 1.5rem;
}

.feature-number {
  background: linear-gradient(45deg, #70A6B2, #5A8A96) !important;
  color: white !important;
  width: 50px !important;
  height: 50px !important;
  border-radius: 50% !important;
  display: flex !important;
  align-items: center !important;
  justify-content: center !important;
  font-weight: 700 !important;
  font-size: 1.2rem !important;
  flex-shrink: 0;
}

.feature-title {
  font-weight: 600 !important;
  color: #2C3E50 !important;
  margin-bottom: 0.5rem !important;
}

.feature-description {
  color: #5A6C7D !important;
  line-height: 1.6 !important;
}

.illustration-container {
  position: relative;
  height: 400px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.illustration-avatar {
  position: absolute !important;
  width: 120px !important;
  height: 120px !important;
  background: linear-gradient(45deg, #70A6B2, #5A8A96) !important;
  color: white !important;
  box-shadow: 0 8px 25px rgba(112, 166, 178, 0.3) !important;
}

.illustration-1 {
  top: 20%;
  left: 10%;
  animation: float 3s ease-in-out infinite;
}

.illustration-2 {
  top: 50%;
  right: 20%;
  animation: float 3s ease-in-out infinite 1s;
}

.illustration-3 {
  bottom: 20%;
  left: 30%;
  animation: float 3s ease-in-out infinite 2s;
}

@keyframes float {
  0%, 100% { transform: translateY(0px); }
  50% { transform: translateY(-20px); }
}

/* Passion section */
.passion-section {
  background: linear-gradient(135deg, #F8F9FA, #E9ECEF);
  padding: 6rem 0;
}

.internship-button {
  background: linear-gradient(45deg, #FF6B35, #F7931E) !important;
  color: white !important;
  font-weight: 600 !important;
  padding: 1rem 2rem !important;
  border-radius: 8px !important;
  text-transform: none !important;
  font-size: 1.1rem !important;
  box-shadow: 0 4px 15px rgba(255, 107, 53, 0.3) !important;
  transition: all 0.3s ease !important;
}

.internship-button:hover {
  transform: translateY(-2px) !important;
  box-shadow: 0 6px 20px rgba(255, 107, 53, 0.4) !important;
}

/* Benefits section */
.benefits-section {
  background: white;
  padding: 6rem 0;
}

.benefit-card {
  background: white !important;
  border-radius: 16px !important;
  box-shadow: 0 8px 25px rgba(0, 0, 0, 0.1) !important;
  transition: all 0.3s ease !important;
  height: 100% !important;
  border: none !important;
  overflow: hidden !important;
}

.benefit-card:hover {
  transform: translateY(-8px) !important;
  box-shadow: 0 15px 35px rgba(0, 0, 0, 0.15) !important;
}

.benefit-content {
  text-align: center !important;
  padding: 2rem !important;
}

.benefit-icon {
  margin-bottom: 1.5rem;
}

.benefit-title {
  font-weight: 600 !important;
  color: #2C3E50 !important;
  margin-bottom: 1rem !important;
}

.benefit-description {
  color: #5A6C7D !important;
  line-height: 1.6 !important;
}

/* Women empowerment section */
.women-empowerment-section {
  background: linear-gradient(135deg, #FFF8F0, #FEF5E7);
  padding: 6rem 0;
  border-radius: 20px;
  margin: 2rem 0;
}

.women-illustration {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 300px;
}

/* Join team section */
.join-team-section {
  background: white;
  padding: 6rem 0;
}

.section-subtitle {
  color: #70A6B2 !important;
  font-weight: 500 !important;
  text-align: center;
  margin-bottom: 2rem !important;
}

.job-card {
  background: white !important;
  border-radius: 16px !important;
  box-shadow: 0 8px 25px rgba(0, 0, 0, 0.1) !important;
  transition: all 0.3s ease !important;
  height: 100% !important;
  border: none !important;
}

.job-card:hover {
  transform: translateY(-8px) !important;
  box-shadow: 0 15px 35px rgba(0, 0, 0, 0.15) !important;
}

.job-posted {
  color: #70A6B2 !important;
  font-weight: 500 !important;
  margin-bottom: 0.5rem !important;
}

.job-title {
  font-weight: 600 !important;
  color: #2C3E50 !important;
  margin-bottom: 1rem !important;
}

.apply-button {
  background: linear-gradient(45deg, #70A6B2, #5A8A96) !important;
  color: white !important;
  font-weight: 600 !important;
  padding: 0.75rem 1.5rem !important;
  border-radius: 8px !important;
  text-transform: none !important;
  transition: all 0.3s ease !important;
}

.apply-button:hover {
  background: linear-gradient(45deg, #5A8A96, #4A7A86) !important;
  transform: translateY(-2px) !important;
}

/* Utility classes */
.text-center {
  text-align: center !important;
}

/* Responsive Design */
@media (max-width: 768px) {
  .hero-title {
    font-size: 2.5rem !important;
  }
  
  .hero-subtitle {
    font-size: 1.2rem !important;
  }
  
  .section-title {
    font-size: 2rem !important;
  }
  
  .job-search-form {
    padding: 1.5rem;
  }
  
  .feature-item {
    flex-direction: column;
    text-align: center;
    gap: 1rem;
  }
  
  .illustration-container {
    height: 300px;
  }
  
  .illustration-avatar {
    width: 80px !important;
    height: 80px !important;
  }
}

@media (max-width: 480px) {
  .hero-title {
    font-size: 2rem !important;
  }
  
  .hero-subtitle {
    font-size: 1rem !important;
  }
  
  .section-title {
    font-size: 1.8rem !important;
  }
  
  .job-search-form {
    padding: 1rem;
  }
}
